.p {
  width: 30%;
  height: 30vh;
  margin: 20px 10px;
  border: 2px solid rgb(211, 230, 211);
  border-radius: 10px 10px 0px 0px;
  overflow: hidden;
}

.p-browser {
  height: 20px;
  background-color: rgb(211, 230, 211);
  display: flex;
  align-items: center;
  position: sticky;
  z-index: 2;
}

.p-circle{
    width: 6px;
    height: 6px;
    background-color: white;
    margin: 3px;
    border-radius: 50%;
}

.p-img{
    width: 100%;
    transition: all 10s ease;
}
.p:hover .p-img{
    transform: translateY(-100%);
}


@media screen and (max-width:480px){

  .p{
    width: 40%;
    height: 20vh;
  }

  .p-img {
    width: 100%;
    animation: move-up-down 5s ease-in-out infinite alternate;
  }
  
  @keyframes move-up-down {
    0% {
      transform: translateY(0);
    }
    50% {
      transform: translateY(-100%);
    }
    100% {
      transform: translateY(0);
    }
  }
  
}